@import url(https://fonts.googleapis.com/css2?family=Andika+New+Basic:ital,wght@0,400;0,700;1,400;1,700&display=swap);
*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  background-color: #333;
  color: #fff;
  font-size: 16px;
  font-family: "Andika New Basic", sans-serif;
  margin: 0 25px;
  overflow-x: hidden;
}

.error {
  color: red;
  text-align: center;
}

.graphs {
  display: -webkit-flex;
  display: flex;
}

.graph-wrapper {
  margin: 75px 0;
  width: 50%;
}

@media only screen and (max-width: 800px) {
  .graphs {
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  .graph-wrapper {
    margin: 25px 0;
    width: 100%;
  }
}

.loader {
  height: 100vh;
  width: 100vw;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.lds-ellipsis {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.lds-ellipsis div {
  position: absolute;
  top: 33px;
  width: 13px;
  height: 13px;
  border-radius: 50%;
  background: #fff;
  -webkit-animation-timing-function: cubic-bezier(0, 1, 1, 0);
          animation-timing-function: cubic-bezier(0, 1, 1, 0);
}
.lds-ellipsis div:nth-child(1) {
  left: 8px;
  -webkit-animation: lds-ellipsis1 0.6s infinite;
          animation: lds-ellipsis1 0.6s infinite;
}
.lds-ellipsis div:nth-child(2) {
  left: 8px;
  -webkit-animation: lds-ellipsis2 0.6s infinite;
          animation: lds-ellipsis2 0.6s infinite;
}
.lds-ellipsis div:nth-child(3) {
  left: 32px;
  -webkit-animation: lds-ellipsis2 0.6s infinite;
          animation: lds-ellipsis2 0.6s infinite;
}
.lds-ellipsis div:nth-child(4) {
  left: 56px;
  -webkit-animation: lds-ellipsis3 0.6s infinite;
          animation: lds-ellipsis3 0.6s infinite;
}
@-webkit-keyframes lds-ellipsis1 {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@keyframes lds-ellipsis1 {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@-webkit-keyframes lds-ellipsis3 {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  100% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
}
@keyframes lds-ellipsis3 {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  100% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
}
@-webkit-keyframes lds-ellipsis2 {
  0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
  100% {
    -webkit-transform: translate(24px, 0);
            transform: translate(24px, 0);
  }
}
@keyframes lds-ellipsis2 {
  0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
  100% {
    -webkit-transform: translate(24px, 0);
            transform: translate(24px, 0);
  }
}

.repos h2 {
  margin-right: 15px;
  font-size: 2rem;
}

.repos h2 span {
  text-decoration: none;
  font-size: 0.8rem;
  color: rgb(131, 124, 124);
}
.repos .sort {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: baseline;
          align-items: baseline;
}

select {
  outline: none;
  width: 90px;
  padding: 5px;
  font-size: 16px;
  line-height: 1;
  border: 0;
  border-radius: 5px;
  height: 34px;
  background: url(http://cdn1.iconfinder.com/data/icons/cc_mono_icon_set/blacks/16x16/br_down.png)
    no-repeat right #ddd;
  appearance: none;
  -moz-appearance: none; /* Firefox */
  -webkit-appearance: none; /* Safari and Chrome */
  background-position-x: 70px;
}

.cards {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  margin-bottom: 25px;
}

.card {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  width: 30%;
  padding: 10px 25px;
  margin: 10px 0;
  background-color: #393838;
  border: 1px solid transparent;
  border-radius: 7%;
  cursor: pointer;
}

.card * {
  margin-bottom: 0;
}
.card h3 {
  font-size: 1.8rem;
}

.repoicon {
  position: relative;
  top: 5px;
  margin-right: 8px;
}

@media only screen and (max-width: 800px) {
  .card {
    width: 100%;
  }
}

.userInfo {
  margin-top: 3rem;
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.userInfo img {
  max-height: 150px;
  max-width: 150px;
  border-radius: 50%;
  border: 10px solid #6565df;
  margin: 0 auto;
}

.userInfo h1,
.userInfo h2 {
  font-family: "Andika New Basic", sans-serif;
}

.userInfo h1 {
  font-size: 2.5rem;
}

.userInfo a {
  text-decoration: none;
  color: #6565df;
}

.generalInfo {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}
.generalInfo span {
  font-size: 1.1rem;
  margin: 0.5rem;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.generalInfo svg {
  margin-right: 0.5rem;
}

.followersInfo {
  margin-top: 1rem;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.followersInfo div {
  margin: 1em;
  background-color: #393838;
  padding: 0 3rem;
  font-size: 0.7rem;
  border-radius: 10%;
}

.followersInfo .counter {
  font-size: 1.4rem;
  font-weight: bold;
}

@media only screen and (max-width: 600px) {
  .followersInfo {
    -webkit-flex-direction: column;
            flex-direction: column;
    margin: 0 auto;
  }
}

.wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  padding-top: 1rem;
}

/* Typo */
.searchP {
  font-size: 5em;
  font-family: "Andika New Basic", sans-serif;
  font-weight: bold;
}

.gradiantTxt {
  background: -webkit-linear-gradient(45deg, #054be0, #8585ce);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.gitIcon {
  font-size: 7em;
  color: #6565df;
}

/* Input */
.searchInput {
  height: 5rem;
  width: 32rem;
  padding: 0 1rem;
  max-width: 75vw;
  border: none;
  font-size: 2rem;
  background-color: #444;
  color: #6565df;
}

.searchInput:focus {
  outline: currentColor 0px none;
  border: none;
}

